@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 14px;
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}

h1 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.header {
  background-image: url("../../../images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header__heading {
  color: white;
}
.header__input {
  padding: 1em;
  border-radius: 1em;
  position: relative;
}
.header__input::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  background-color: red;
  z-index: 5;
  padding: 5px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: white;
  border-radius: 100px;
  z-index: 2;
  border: 2px solid black;
  padding: 0;
}
.info > div {
  padding: 1em;
  margin: 0.5em;
}
.info__label {
  font-size: 0.8em;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #969696;
  margin: 0.5em 0;
}
.info__text {
  font-weight: bold;
  font-size: 1.5em;
}

.map {
  height: 40vh;
}